@import url(https://fonts.googleapis.com/css?family=Black+Ops+One|Nova+Square|Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding : 0;
}
/*
	font-family: 'Press Start 2P', cursive; - display output
	font-family: 'Black Ops One', cursive; - buttons
	font-family: 'Nova Square', cursive; - Author - credits
*/

.App {
	overflow : hidden;
	width : 100vw;
	height : 100vh;
	display : grid;
	grid-template-rows : 40% 60%;
	background-color : black;
	grid-row-gap : 4px;
}

.Display {
	color : #FFFFFF;
	display : grid;
	grid-template-rows : auto auto;
	align-items : end;
	align-content : end;
	font-family: 'Press Start 2P', cursive;
}

.input {
	max-height : 100%;
	overflow-y : scroll;
	display : flex;
	align-items : flex-end;
	justify-content : flex-end;
	flex-wrap : wrap;
}

.value {
}

.cursor {
	-webkit-animation : cursorToggle 1s infinite;
	        animation : cursorToggle 1s infinite;
}

.answer {
}

@-webkit-keyframes cursorToggle {
	from {
		opacity : 1;
	}
	to {
		opacity : 0;
	}
}

@keyframes cursorToggle {
	from {
		opacity : 1;
	}
	to {
		opacity : 0;
	}
}

.Numpad {
	height : 100%;
	display : grid;
	grid-template-areas :
		'numpadReset numpadReset numpadLeftArrow numpadRightArrow'
		'numpad7 numpad8 numpad9 numpadPlus'
		'numpad4 numpad5 numpad6 numpadMinus'
		'numpad1 numpad2 numpad3 numpadMultiply'
		'numpad0 numpad0 numpadDot numpadDivide'
		'. . numpadEval numpadEval';
	grid-gap : 2px;
}

.Numpad button {
	font-size : 1.2em;
	border : none;
	font-family: 'Black Ops One', cursive;
	outline : none;
}

.Numpad button i {
	pointer-events : none;
}

.number {
	background-color : white;
}

.operator {
	background-color : gray;
	color : white;
}

